<template>
  <div class="recommend-prod" v-if="productList.length">
    <div class="bg-tag"/>
    <div class="title">
      <span class="text">{{contentType === 1 ? '生态优选' : '推荐商品'}}</span>
      <a href="javascript:;" @click="goPage()">更多 <i></i></a>
    </div>
    <div class="prod-content">
      <div class="prod-row">
        <div class="prod-item" v-for="(item, index) in productList" :key="index" @click="goProduct(item,index)">
          <img :src="item.defaultImageUrl | imageToHttps"/>
          <div class="prod-name">
            {{item.productFullName}}
          </div>
          <div class="prod-price">
            <span class="yuan">￥</span>
            <span class="price-int">{{item.finalPrice | formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{item.finalPrice | formatPrice | formatPriceFloat}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/** 推荐商品、 生态优选 */
export default {
  props: {
    contentType: {
      type: Number, // 0 - 推荐商品  1 - 生态优选
      default: 0,
    },
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
    isCustomPage: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    productList() {
      let list = [];
      if (this.data && this.data.productDetails && this.data.productDetails.length) {
        list = this.data.productDetails;
        list.sort((a, b) => a.sort - b.sort);
      }
      return list.slice(0, 5);
    }
  },
  methods: {
    goPage() {
      const productIds = [];
      this.data.productDetails.map(item => {
        productIds.push(item.productId);
      });
      if (this.contentType) { // 生态优选
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '生态优选',
          modelCode: 'eco_opt',
          site_number: 'null',
          itemCode: '更多',
        })
      } else {
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '推荐商品',
          modelCode: 'recommend',
          site_number: 'null',
          itemCode: '更多',
        })
      }
      // this.data.productDetails.sort((a, b) => a.sort - b.sort);
      console.log('productIds=', productIds.join());
      if (this.isCustomPage) {
        // this.$router.push(`shopSelectList?contentType=${this.contentType}&shareId=${this.storeId}&productIds=${productIds.join()}`);
        this.$router.push(`shopSelectList?contentType=${this.contentType}&storeId=${this.contentType ? '' : this.storeId}&pageModelId=${this.data.id}`);
      } else {
        this.$router.push(`shopSelectList?contentType=${this.contentType}&storeId=${this.contentType ? '' : this.storeId}&storeModelId=${this.data.id}`);
        // this.$router.push(`shopSelectList?contentType=${this.contentType}&storeId=${this.storeId}&productIds=${productIds.join()}`);
      }
    },
    goProduct(prod, index) {
      if (this.contentType) { // 生态优选
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '生态优选',
          modelCode: 'eco_opt',
          site_number: index + 1 + '',
          itemCode: prod.sku + '',
        })
        this.$router.push(`/goodsDetail?productId=${prod.productId}`);
      } else {
        // 25093埋点
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '推荐商品',
          modelCode: 'recommend',
          site_number: index + 1 + '',
          itemCode: prod.sku + '',
        })
        this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${this.storeId}`);
      }
    }
  },
}
</script>
